import { HashRouter, Switch, Route } from "react-router-dom";
import Header from "./Components/Header";
import Home from "./Routes/Home";
import Search from "./Routes/Search";
import Tv from "./Routes/Tv";

function App() {
  return (
    <HashRouter>
      <Header />
      <Switch>
        <Route path="/tv">
          <Tv />
        </Route>
        <Route path="/search">
          <Search />
        </Route>
        <Route 
        //  /    home 
        // /movies/:movieId   home 
        // home    
        // movie      
          path={["/", "/movies/:movieId"]}>
          <Home />
        </Route>
      </Switch>
    </HashRouter>
  );
}

export default App;